<template>
	<div id="order-assign">
		<public-header title="排队"></public-header>
		<div class="content">
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<div class="assign-list" v-if="queues.length > 0">
				<div class="assign-group">
					<div class="assign-item border-1px-t" v-for="(item, index) in queues">
						<div class="assign-title border-1px-b">
							<div class="assign-info">
								<div>当前叫号</div>
								<div class="info" v-if="item.now_number">{{item.now_number}}</div>
								<div class="info" v-else>暂无排队信息</div>
							</div>
							<div class="assign-info">
								<div>桌号分类</div>
								<div class="tables-type">{{item.title}}</div>
							</div>
							<div class="assign-take">
								<div class="takeout">
									<div>取号</div>
									<div>({{item.total}}/{{item.position}})</div>
								</div>
							</div>
						</div>
						<div class="assign-button border-1px-b">
							<router-link tag="div" :to="util.getUrl({path: '/pages/order/assignRecord', query:{id: item.id}})" class="small-button">
								<span class="icon icon-viewgallery"></span>
								查看已领
							</router-link>
							<div class="small-button active" @click="onChangeNotice(item, item.now_number)">
								<span class="icon icon icon-remind2"></span>
								叫号
							</div>
							<div class="small-button active" @click="onChangeStatus(item, 2, item.now_number, index)">
								<span class="icon icon-home2"></span>
								入座
							</div>
							<div class="small-button active" @click="onChangeStatus(item, 3, item.now_number, index)">
								<span class="icon icon-forward"></span>
								跳过
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>暂无排队列表!</p>
			</div>
			</van-pull-refresh>
			<router-link tag="div" class="assign-but" :to="util.getUrl({path: '/pages/order/assignQueue'})">
				<van-button size="normal" block class="bg-info font-16">添加队列</van-button>
			</router-link>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				queues: [],
				isRefresh: false,
				showPreLoading: true
			}
		},
		methods: {
			onLoad(force){
				if (force){
					this.queues = []
				}
				var that = this;
				getData({
					vue: this,
					url: 'manage/order/assign/index',
					success: (res) => {
						that.queues = res.queues;
						this.isRefresh = false;
					}
				});
			},
			onChangeNotice(item, number){
				var that = this;
				that.util.jspost({
					url: 'manage/order/assign/notice',
					data: {
						id: item.id,
						number: number
					},
					confirm: '确定通知吗？',
					success: function(res){
						that.util.$toast(res, 0);
					}
				});
			},
			onChangeStatus(item, status, number, index){
				var that = this;
				if (status == 2){
					var confirmText = '确定该号已入座吗?';
				} else {
					var confirmText = '确定过号吗?';
				}
				that.util.jspost({
					url: 'manage/order/assign/status',
					data: {
						id: item.id,
						status: status,
						number: number
					},
					confirm: confirmText,
					success: function(res){
						that.queues[index].now_number = false;
						if (that.queues[index].total != 0){
							that.queues[index].total = that.queues[index].total -1;
						}
					}
				});
			},
			onPullDownRefresh(){
				this.onLoad();
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#order-assign .content .assign-item{
		background-color: #fff;
		margin-bottom: 10px;
	}
	#order-assign .content .assign-item:last-child{
		margin-bottom: 75px;
	}
	#order-assign .content .assign-item .assign-title{
		display:flex;
		justify-content:flex-start;
		align-items:center;
		text-align:center;
		padding:10px 0;
	}
	#order-assign .content .assign-item .assign-title .assign-info{
		width:33.33%;
		border-right:1px solid #e0e0e0;
		font-size:14px;
		color:#B5B5B5;
	}
	#order-assign .content .assign-item .assign-title .assign-info div{
		line-height: 21px;
	}
	#order-assign .content .assign-item .assign-title .assign-info .info{
		font-weight:bold;
		color:#FF2D4B;
		padding-top:20px;
	}
	#order-assign .content .assign-item .assign-title .assign-info .tables-type{
		font-weight:bold;
		padding-top:20px;
		color:#3d4145;
	}
	#order-assign .content .assign-item .assign-title .assign-take{
		width:33.33%;
		font-size:14px;
		color:#FFF;
	}
	#order-assign .content .assign-item .assign-title .assign-take .takeout{
		width:70%;
		margin:0 auto;
		border-radius:5px;
		background-color:#31AAFB;
	}
	#order-assign .content .assign-item .assign-title .assign-take .takeout div{
		padding: 5px 0;
		line-height: 21px;
	}
	#order-assign .content .assign-item .assign-button{
		display:flex;
		justify-content:flex-end;
		align-items:center;
		padding:10px;
		background-color:#FFF;
		font-size:14px;
	}
	#order-assign .content .assign-item .assign-button .small-button{
		font-size:12px;
		line-height: 24px;
		text-align:center;
		cursor:pointer;
		border-radius:5px;
		color:#888;
		padding:3px 7px;
		margin-left:10px;
		border:1px solid #AFAFAF;
	}
	#order-assign .content .assign-item .assign-button .small-button.active{
		color:#31AAFB;
		border-color:#31AAFB;
	}
	#order-assign .content .assign-item .assign-button .small-button .icon{
		font-size: 15px;
	}
	#order-assign .content .assign-but{
		position:fixed;
		bottom:0;
		left:0;
		background-color:#fff;
		width:100%;
		padding: 10px;
	}
</style>


